<template>
  <div class="home-container">
    <el-container class="home-container">
      <el-header>
        <div>
            <img src="../assets/heima.png" />
            <span>电商后台管理系统</span>
        </div>
        <el-button type="info" @click="logout">退出</el-button>
      </el-header>
      <el-container>
         
        <el-aside :width="isCollapse ? '64px' : '200px'">
            <el-button type="info" @click="Onchange" class="btn">|||</el-button>
            <el-menu
              router
              unique-opened
              background-color="#545c64"
              text-color="#fff"
              :collapse="isCollapse"
              :default-active="activePach"
              :collapse-transition="false"
              active-text-color="#409eff">
              <el-submenu  v-for="item in menuList" :key="item.id" :index="item.id + ''" > 
                <template slot="title">
                  <i :class="iconsObj[item.id]"></i>
                  <span>{{item.authName}}</span>
                </template>
                  <el-menu-item  v-for="items in item.children" :key="items.id" :index="'/' + items.path" @click="saveNavState('/' + items.path)">
                  <i class="el-icon-menu"></i> 
                  <span slot="title">{{items.authName}}</span>
                </el-menu-item>
              </el-submenu>          
            </el-menu>
        </el-aside>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  data(){
    return{
      menuList:[],
      iconsObj:{
        '125':'el-icon-location',
        '103':'el-icon-menu',
        '101':'el-icon-document',
        '102':'el-icon-setting',
        '145':'el-icon-location',        
      },
      isCollapse:false,
      activePach:'',
    }
  },
  created() {
    this.getMenuList()
    this.activePach = window.sessionStorage.getItem('activePath')
  },
  methods: {
    logout() {
      window.sessionStorage.clear()
      this.$router.push('/login')
    },
    Onchange(){
      this.isCollapse = !this.isCollapse
    },
    async getMenuList() {
      const { data: res } = await this.$http.get('menus')
      console.log(res)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.menuList = res.data
    },
    saveNavState(activePath){
      console.log(activePath)
      window.sessionStorage.setItem('activePath',activePath)
      this.activePach = activePath
    }
  }
}
</script>

<style lang="less" scoped>
  .el-header {
    background-color: #373d41;
    display: flex;
    justify-content: space-between;
    padding-left:0;
    align-items: center;
    color: #fff;
    font-size: 20px;
    > div{
      display: flex;
      align-items: center;
      span{
        margin-left: 15px;
      }
    }
  }
  .el-aside{
    background-color: #333744;
    .el-menu{
      border-right: none;
    }
    .btn{
      width: 100%;
    }
  }
  .el-main{
    background-color: #EAEDF1;
  }
  .home-container{
    height: 100%;
  }
</style>
